.modal-preview {
  width: 90% !important; }

.bg-half {
  position: fixed;
  background: #576574;
  background: linear-gradient(120deg, #576574 0%, #54a0ff 50%, #2e86de 100%);
  top: 0;
  left: 0;
  width: 100%;
  height: 50vh; }

.wrapper {
  position: fixed;
  top: 50%;
  left: 0;
  right: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  overflow-y: auto; }

.hour-options {
  background: linear-gradient(to top, #1dd1a1, #0abde3), url(/static/media/tile.87dd4004.png);
  background-size: 15%;
  background-blend-mode: multiply;
  border-width: 0;
  color: white;
  transition: 500ms ease-in-out; }
  .hour-options h4::-moz-selection {
    background: transparent; }
  .hour-options h4::selection {
    background: transparent; }
  .hour-options h4:hover {
    cursor: pointer; }
  .hour-options:hover {
    box-shadow: 0px 3px 17px -5px rgba(0, 0, 0, 0.87);
    transition: 500ms ease-in-out; }
  .hour-options.active-time {
    background: linear-gradient(to top, #222f3e, #576574);
    border: 0.5px solid #576574;
    box-shadow: 0px 3px 17px -5px rgba(0, 0, 0, 0.5); }
  .hour-options .front-office {
    position: absolute;
    top: -5px;
    right: -5px;
    background: #ee5253;
    border-radius: 25%;
    padding: 1px 8px;
    color: white; }

#dash-wrapper {
  overflow-y: hidden; }

.login-btn {
  padding: 15px;
  display: inline-block;
  background: white;
  border-radius: 0.25em;
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 40; }

.btn-group button:not(:first-child):not(:nth-last-child()) {
  border-left: 1px solid #999 !important;
  border-right: 1px solid #999 !important; }

.logout-btn {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: 0.25px solid #bbb;
  padding: 5px 10px; }

.overflow-scroll {
  height: 100%;
  overflow-y: auto; }

